import {actions} from "mirrorx";
// services
import * as api from "./service";
// 
import { processData } from "utils";


export default {
    //  Store 
    name: "app",
    //  Model  state
    initialState: {
        current: '',//key
        openKeys: [],//key
        currentRouter:'',//
        menu: [],//
        expanded: false,//
        firstUrl: '',
        curentOpenKeys: [],//key
        submenuSelected: '',//key
        isOpenTab: true,//
        menus:[],//
        tabNum:0,//
        showNotice:0,//
        curNum:0,//
        num:0,
        clientHeight:document.body.clientHeight,
        reload:0
    },
    reducers: {
        /**
         *  Redux  Reducer
         * @param {*} state
         * @param {*} data
         */
        updateState(state, data) { //state
            return {
                ...state,
                ...data
            };
        }

    },
    effects: {
        /**
         * 
         * @param {*} param
         * @param {*} getState
         */
        async loadList(param, getState) {
            //  Loading 

            //  getList 
            let res = processData(await api.getList());
            let data ;
            if(!res || !res.data){
                data = [{
                    "location" : "pages/default/index.js",
                    "name" : "",
                    "menustatus" : "Y",
                    "children" : null,
                    "icon" : "iconfont icon-C-home",
                    "openview" : "curnpage",
                    "menuId" : "M0000000000002",
                    "urltype" : "plugin",
                    "id" : "index",
                    "isDefault" : null,
                    "licenseControlFlag" : 0
                }]
            }else{
                data = res.data;
            }
            actions.app.updateState({
                menu: data,
            });

            return data;
        },


        /**
         * 
         * @param {*} param
         * @param {*} getState
         */
        async loadUserMenuList(param, getState) {
            //  Loading 

            //  loadUserMenuList 
            let res = processData(await api.loadUserMenuList());

            actions.app.updateState({
                userMenus: res.data,
            });

            return res.data;
        },
        /**
         * 
         * @param {*} param
         * @param {*} getState
         */
        async loadUnReadMsg(param, getState) {
            //  Loading 

            //  loadUserMenuList 
            let res = processData(await api.loadUnReadMsg());


            return res.data;
        },
        /**
         * 
         * @param {*} param
         * @param {*} getState
         */
        async getWebPushInfo(param, getState) {
            //  Loading 

            //  loadUserMenuList 
            let res = processData(await api.getWebPushInfo());


            return res.data;
        }
    }
};


